.manage-bank-card {
  background: #1d252c;
  min-height: 100%;
  ul {
    margin-top: 0;
  }
  li:first-child {
    margin: 0;
  }
  li {
    position: relative;
    overflow: hidden;
    color: #fff;
    margin-top: -215px;
    box-shadow: 0px 8px 10px 8px rgba(0,0,0,0.5);
    transition-duration: .3s;
  }

  .bank-info {
    position: relative;
    width: 100%;
    height: 180px;
    padding-top: 25px;
  }
  .bank-icon {
    width: 33px;
    height: 33px;
    line-height: 33px;
    background: #fff;
    border-radius: 25px;
    display: block;
    text-align: center;
    margin: 0 10px 0 15px;
    float: left;
  }
  .bank-txt {
    position: relative;
    float: left;
    z-index: 10;
  }
  h4 {
    font-size: 16px;
    font-weight: 400;
    margin: 0 0 10px  0;
    line-height: 1;
  }
  .bank-card {
    width: 100%;
    height: 180px;
    background: #43679c;
    border-radius: 5px;
    border-top: 1px solid rgba(255,255,255,0.2);
  }
  .bank-type {
    font-size: 12px;
    margin-bottom: 3px;
    display: block;
  }
  .num-txt {
    display: inline-block;
    font-family: 'Farrington7B_Qiqi',sans-serif;
    font-weight: 700;
    font-size: 180px;
    width: 60px;
    letter-spacing: 2px;
    text-shadow: 0px 1px 1px rgba(0,0,0,0.5);
  }
  .bank-num {
    .hidden {
      letter-spacing: 3px;
      display: inline-block;
      margin-right: 110px;
    }
  }
  .icon:before {
    position: absolute;
    right: 0;
    bottom: -20px;
    font-size: 715px;
    color: #fff;
    opacity: 0.1;
  }
  .bank-del {
    width: 60px;
    height: 180px;
    line-height: 140px;
    background: rgba(0,0,0,0.5);
    position: absolute;
    right: -60px;
    top: -1px;
    border-radius: 5px;
    text-align: center;
  }
  .bank-del-active {
    -webkit-transform:translate(-60px,0);
    transform:translate(-60px,0);
    -webkit-transition-duration: .3s;
    transition-duration: .3s;
    overflow: visible;
  }
  .icon-del {
    width: 33px;
    height: 33px;
    background-size: 100%;
  }
  .bank-ABC,.bank-minsheng,.bank-CBHB {
    background: #36807c;
  }
  .bank-CMBC,.bank-ICBC,.bank-BC,.bank-GDB,.bank-CITIC,.bank-huaxia,.bank-BOB {
    background: #c9404f;
  }
  .bank-CEB,.bank-pingan,.bank-PSBC,.bank-Shanghai {
    background: #e49d26;
  }
}
